/* CONTAINERS */

.safeBorder {
    position: absolute;
    width: 100%;
    height: auto;
    top: 50%;
    transform: translateY(-50%);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

.appContainer {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: calc(var(--container-width));
    height: calc(var(--container-height));
    transform: translate(-50%,-50%) scale(calc(var(--scaling)));
}

.xboxComponent {
    position: absolute;
    display: inline-block;
    width: inherit;
    width: inherit;
    height: inherit;
    /* background-color: red; */
}

.mainContainer {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: inherit;
    height: inherit;
}

.bladeMask {
    position: absolute;
    left: 50%;
    top: 50%;
    width: inherit;
    height: inherit;
    transform: translate(-50%, -50%);
    z-index: 10;
    pointer-events: none;
}


.bladeContainerMask {
    position: absolute;
    width: inherit;
    height: inherit;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}




.topContainerBorder {
    position: absolute;
    width: 100%;
    height: 5px;
    background-image: linear-gradient(0deg, #B3A89E 0%, #E5E5E5 80%);
    box-shadow: 0 1px 5px 0 black;
    z-index: 1;
}

.bottomContainerBorder {
    position: absolute;
    width: 100%;
    height: 5px;
    top: calc(100% - 5px);
    background-image: linear-gradient(0deg, #B3A89E 0%, #E5E5E5 80%);
    box-shadow: 0 -1px 5px 0 black;
    z-index: 1;
}


.curvedGlassOverlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: url("../assets/images/glass-overlay.png");
    background-repeat: no-repeat;
    background-size: cover;
    opacity: 0;
    pointer-events: none;
}



.blade > p {
    position: relative;
    display: block;
    top: 50%;
    transform: rotate(90deg) translateX(-50%);
    white-space: nowrap;
}


.topBorder {
    position: absolute;
    width: calc(99%);
    height: 90px;
    left: 51%;
    transform: translateX(-50%);
    box-shadow: inset 0 -1px 7px 0px black;
    border-radius: 0px 0px 70px 70px;
}

.bottomBorder {
    position: absolute;
    width: calc(100%);
    height: 90px;
    top: 89%;
    left: 51%;
    transform: translateX(-50%);
    box-shadow: inset 0 -1px 7px 0px black;
    border-radius: 80px 80px 0px 0px;
}

.leftEdge {
    position: absolute;
    width: 100px;
    height: 110%;
    left: -100px;
    transform: translate(60%, -5%);
    background: linear-gradient(0deg, #B3A89E 0%, #E5E5E5 80%);
    border-radius: 50%;
    border: 8px solid rgb(99, 99, 99);
}

.rightEdge {
    position: absolute;
    width: 100px;
    height: 110%;
    left: calc(100% - 100px);
    transform: translate(60%, -5%);
    background: linear-gradient(0deg, #B3A89E 0%, #E5E5E5 80%);
    border-radius: 50%;
    border: 8px solid rgb(99, 99, 99);
}



.buttonContainer {
    position: absolute;
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 90px;
    top: 89%;
    padding: 0 35px;
}

.buttons {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 30px;
    height: 100%;
    margin-top: 5px;
}

.buttonStyle {
    width: 100%;
    height: 30px;
    background-size: contain;
    background-repeat: no-repeat;
}

#yButton {
    background-image: url('../assets/images/y_button.png');
    margin-bottom: 5px;
    margin-left: 20px;
}

#xButton {
    background-image: url('../assets/images/x_button.png');
}

#bButton {
    background-image: url('../assets/images/b_button.png');
    margin-bottom: 5px;
    margin-left: -20px;
}

#aButton {
    background-image: url('../assets/images/a_button.png');
}


.backgroundsContainer {

}

.pageContentArea {
    position: absolute;
    width: calc(920px);
    height: 100%;
    max-width: calc(var(--container-width));
    max-height: var(--container-height);
    left: 50%;
    transform: translateX(-50%);
    overflow: hidden;
}

.staticContent {
    position: absolute;
    width: 100%;
    height: inherit;
    z-index: 1;
    pointer-events: none;
}









.marketplaceBackground {
    position: absolute;
    width: calc(100%);
    height: 100%;
    background-image: url("../assets/images/marketplace_background.png");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: 75% 50%;
    z-index: -10;
}

.xboxliveBackground {
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(231deg, #f78833, #f3ab58);
    background-size: 400% 400%;
    z-index: -9;
}

@keyframes xboxlive_color_scheme_gradient {
    0%{background-position:0% 87%}
    50%{background-position:100% 14%}
    100%{background-position:0% 87%}
}

.gamesBackground {
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(231deg, #69cf38, #43903d);
    background-size: 400% 400%;
    z-index: -8;
}

@keyframes games_color_scheme_gradient {
    0%{background-position:0% 87%}
    50%{background-position:100% 14%}
    100%{background-position:0% 87%}
}



.mediaBackground {
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(231deg, #449aed, #297bc4);
    background-size: 400% 400%;
    z-index: -7;
}

@keyframes media_color_scheme_gradient {
    0%{background-position:0% 87%}
    50%{background-position:100% 14%}
    100%{background-position:0% 87%}
}

.systemBackground {
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(250deg, #8E6ED1, #7057a7);
    background-size: 400% 400%;
    z-index: -6;
}

@keyframes system_color_scheme_gradient {
    0%{background-position:0% 71%}
    50%{background-position:100% 30%}
    100%{background-position:0% 71%}
}

.sectionHeading {
    position: absolute;
    left: 30px;
    top: 55px;
    z-index: 10;
    color: white;
    font-weight: 500;
}



/*System Tray*/

.systemTrayContainer {
    position: absolute;
    width: calc(100% - 200px);
    height: 70px;
    left: 50%;
    top: 76%;
    transform: translateX(-50%);
    margin-top: 15px;
    cursor: pointer;
}

.systemTrayContainer > p {
    position: absolute;
    top: 50%;
    left: 85px;
    transform: translateY(-60%);
    font-size: 1.4rem;
}

.trayEllipse {
    width: 70px;
    height: 100%;
    border-radius: 50%;
    box-shadow: inset 0 0 7px 0 black;
}

.trayRect {
    position: absolute;
    width: 100%;
    height: 40px;
    top: 50%;
    left: 15px;
    transform: translateY(-50%);
    border-radius: 20px;
    box-shadow: inset 0 0 4px 0 black;
}

.trayTriangleButton {
    position: absolute;
    width: 0;
    height: 0;
    top: 50%;
    left: 30px;
    transform: translateY(-50%);
    border-top: 10px solid transparent;
    border-right: 10px solid rgba(0, 0, 0, 0.466);
    border-bottom: 10px solid transparent;
}

.trayRectButton {
    position: absolute;
    width: 4px;
    height: 19px;
    top: 50%;
    left: 43px;
    transform: translateY(-50%);
    box-shadow: inset 0 0 2px 0 rgba(0, 0, 0, 0.466);
}


/* Navigation Buttons */

.arrowContextButtonContainer {
    position: absolute;
    display: flex;
    justify-content: center;
    width: 220px;
    height: 80px;
    left: 50%;
    top: 92%;
    transform: translateX(-50%);
    border-radius: 65px;
    background-color: rgba(0, 0, 0, 0.744);
    padding-top: 10px;
    z-index: 100;
    opacity: 0.8;
    transition: opacity 0.3s ease-in-out;

    &:hover {
        opacity: 1;
    }
}

.xboxHomeLogo {
    position: relative;
    order: 2;
    width: 100%;
    max-width: 80px;
    background-image: url("../assets/images/xbox_home_logo.svg");
    background-repeat: no-repeat;
    background-size: contain;
    cursor: pointer;
    z-index: 1000;


}

.ellipseGlow {
    position: relative;
    display: inline-block;
    width: 56px;
    height: 56px;
    transform: translate(0%,0%);
    border-radius: 50%;
    transition-duration: 0.3;
    z-index: 0;

    &:hover {
        box-shadow: 0 0 2px #68CC39b5, 0 0 8px #68CC39b5, 0 0 16px #68CC39b5, 0 0 24px #68CC39b5, 0 0 32px #336924b5;
    }
}


.leftArrow {
    order: 1;
    width: 100%;
    max-width: 40px;
    margin-right: 20px;
    background-image: url("../assets/images/nav-arrow-left.png");
    background-repeat: no-repeat;
    background-size: contain;

    &:hover {
        cursor: pointer;
    }
}

.rightArrow {
    order: 3;
    width: 100%;
    max-width: 40px;
    margin-left: 0px;
    background-image: url("../assets/images/nav-arrow-right.png");
    background-repeat: no-repeat;
    background-size: contain;

    &:hover {
        cursor: pointer;
    }
}


@media only screen and (max-width: 765px) {
    .appContainer {
        transform: translate(-51%,-50%) scale(calc(var(--scaling)));
    }
    /* .bladeMask {
        overflow: hidden;
    } */

}