.profileContainer {
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: 
    "profile-img profile-desc profile-desc profile-desc";
    width: 400px;
    height: 95px;
    /* top: 100px; */
    max-height: 135px;
    border-radius: 8px;
    text-align: left;
    font-size: 1.5rem;
    /* overflow: hidden; */
    box-shadow: 0px 0px 4px 0px black;
        &:hover{
            cursor: pointer;
            background: linear-gradient(90deg, rgba(255, 255, 255, 0) 2%, rgba(255, 255, 255, 0.467), rgba(255, 255, 255, 0) 98%);
        }
}

.profileContainer > p {
    grid-area: profile-name;
    position: relative;
    width: inherit;
    height: 35px;
    left: 10px;
    font-size: var(--screen-heading-size);
    font-weight: 500;
    z-index: 2;
}

.profileContainer > p:after {
    content: "";
    position: absolute;
    width: inherit;
    height: inherit;
    left: -10px;
    top: 0;
    background: linear-gradient(270deg, #00000000, #00000043 75%);
}


.profileImgContainer {
    grid-area: profile-img;

    /* background-color: violet; */
}

.profileIcon {
    position: relative;
    width: 64px;
    height: 64px;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    background-image: url('../assets/images/profile_icon.jpeg');
    background-size: contain;
    background-repeat: no-repeat;
    box-shadow: inset 0px 0px 1px 2px rgba(0, 0, 0, 0.405);
    overflow: hidden;
}

.iconGloss {
    position: absolute;
    width: calc(100% - 10px);
    height: calc(100% - 5px);
    left: 50%;
    top: 50%;
    transform: translate(-50%,-45%);
    border-radius: 5px;
    background: linear-gradient(rgba(255, 255, 255, 0.446), rgba(0, 0, 0, 0));
}


.profileDescription {
    grid-area: profile-desc;
    z-index: 2;
    display: grid;
    height: 100%;
    max-height: 75px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: minmax(25px);
    grid-template-areas:
    "rep-title . rep-stars rep-stars"
    "gamerscore-title gamerscore-title . gamerscore-value"
    "zone-title . . zone-status";
    margin-right: 15px;
    margin-top: 10px;
    /* height: 90%; */
    font-size: var(--text-link-size);
}


.gamerscoreTitle {
    grid-area: gamerscore-title;
    /* background-color: lightsteelblue; */
}

.gamerscoreValue {
    grid-area: gamerscore-value;
    /* background-color: darkkhaki; */
    text-align: right;
    margin-right: 9px;
}

.zoneTitle {
    grid-area: zone-title;
    /* background-color: lightyellow; */
}

.zoneStatus {
    grid-area: zone-status;
    /* background-color: darkturquoise; */
    text-align: right;
    margin-right: 10px;
}


