/*** Outer blade container and individual blade containers ***/

.bladeContainer {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    left: 170px;
    z-index: 10;
    transition: transform 0.8s ease-out;
    /* opacity: 0; */

}

.centeredMask {
    position: relative;
    width: calc(var(--container-width));
    height: calc(var(--container-height));
    left: 50%;
    transform: translateX(-50%);
}

.centeredContent {
    position: absolute;
    width: var(--container-width);
    height: var(--container-height);
    transform: translate(-50%, -50%);
    left: 50%;
    top: 50%;
}

.blade {
    position: absolute;
    height: calc(100% + 0px);
    width: 100px;
    transition: transform 0.6s ease-in;
    overflow: visible;
}

.animateTransparentOpeningContainer {
    position: absolute;
    left: 50px;
    width: calc(var(--container-width) - 100px);
    height: 100%;
    background-repeat: no-repeat;
    background-size: contain;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    
}

.dashboardWhiteUnderlay {
    position: absolute;
    left: 10px;
    top: 0;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50% 50%;
    opacity: 0;
    transition: opacity 0.4s;
    transition-delay: 0.3s;
    transition-timing-function: ease-in;
    display: 'none';
}

.marketplaceWhiteUnderlay {
    background-image: url("../assets/images/dashboard_white_underlay.png");
}

.dashboardUnderlayImage {
    background-image: url("../assets/images/dashboard_white_underlay.png");
}


.dashboardUnderlayActive {
    opacity: 0.8;
    display: initial;
}

