.centerBlockExpand {
    position: absolute;
    width: 100%;
    height: 350px;
    background-color: rgba(219, 219, 219, 0.2);
    opacity: 0;
}

@keyframes expandBox {
    0% {
        height: 600px;
    }
    40% {
        height: 200px;
    }
    80% {
        height: 630px;
    }
    100% {
        height: 600px;
    }
}

.leftEdge {
    position: absolute;
    width: 100px;
    height: 110%;
    left: -120px;
    transform: translate(60%, -5%);
    background: linear-gradient(0deg, #B3A89E 0%, #E5E5E5 80%);
    border-radius: 50%;
    border: 8px solid rgb(99, 99, 99);
}

.rightEdge {
    position: absolute;
    width: 100px;
    height: 110%;
    left: calc(100% - 100px);
    transform: translate(60%, -5%);
    background: linear-gradient(0deg, #B3A89E 0%, #E5E5E5 80%);
    border-radius: 50%;
    border: 8px solid rgb(99, 99, 99);
}


.bladeGroup {
    position: relative;
    height: inherit;
    width: 95px;
    left:0;
    overflow: visible;
}

.bladeImgContainer {
    position: absolute;
    display: inline-block;
    width: inherit;
    height: inherit;
    top: 0;
    left: 0;
    background-repeat: no-repeat;
    background-size: auto;
    background-position-y: -10px;
    pointer-events: none;
}

.svgInteractiveContainer {
    position: absolute;
    left: 0;
    top: 0;
    cursor: pointer;
}


