.leftGroup {
    position: absolute;
    height: inherit;
    left: -90px;
}

.rightGroup {
    position: absolute;
    height: inherit;
    left: calc(100% - 270px);

    .blade:nth-child(1){
        transform: translateX(0px);
    }

    .blade:nth-child(2){
        transform: translateX(55px);
    }
    
    .blade:nth-child(3){
        transform: translateX(110px);
    }
    
    .blade:nth-child(4) {
        transform: translateX(165px);
    }
    
    .blade:nth-child(5) {
        transform: translateX(160px);
    }
}





