/*** SVG STYLING ***/

svg {
    height: auto;
    transform-origin: 50% 50% ;
}

path {
    pointer-events: auto;
    height: 100%;
}


.bladeBase {
    position: absolute;
    z-index: -1;
    width: inherit;
    min-width: auto;
    max-width: 86px;
    height: inherit;
    left: 0;
    fill: url("#paint0_radial_216_168");
    stroke: black;
    stroke-opacity: 0.5;
}

.bladeJewel {
    position: absolute;
    width: inherit;
    min-width: auto;
    max-width: 65px;
    height: inherit;
    left: calc(((100vh) * (1/32)));
    transform: translateX(0);
}

.bladeBaseRight {
    z-index: -1;
    top: 0;
}

.bladeJewelRight {
    top: 0;
    left: calc(((100vh) * (-1/60)));
    transform: translateX(0%);
}

.activeMarketplaceJewelFill {
    fill: url("#paint0_radial_217_185");
}

.activeXboxliveJewelFill {
    fill: url("#paint0_radial_217_189");
    transition: fill 2s;
}

.activeGamesJewelFill {
    fill: url("#paint0_radial_217_181");
}

.activeMediaJewelFill {
    fill: url("#paint0_radial_217_193");
}

.activeSystemJewelFill {
    fill: url("#paint0_radial_217_197");
}

.inactiveBladeFill {
    fill: url("#paint0_radial_216_167");
    transition: fill 2s;
}

.singleBladeGroup {
    height: inherit;
    width: inherit;
}


.transitionBlade {
    position: absolute;
    width: 60%;
    height: 100%;
    background-color: purple;
    opacity: 1;
}
